/**
 * 
 * @returns
 */
import Vue from 'vue';
import Router from 'vue-router';
import VueResource from 'vue-resource';

import Welcome from './../components/Welcome.vue';
import Main from './../components/Main.vue';
import Begin from './../components/Begin.vue';
import Know from './../components/Know.vue';
import Music from './../components/Music.vue';
import Point from './../components/Point.vue';
import User from './../components/User.vue';
import UserInfo from './../components/UserInfo.vue';
import UserList from './../components/UserList.vue';
Vue.use(Router);

export default new Router({
  routes: [{ //
      path: '/', //
      name: 'begin', //
      component: Begin //
    },
    {
      path: '/main',
      name: 'main',
      component: Main
    },
    {
      path: "/welcome",
      name: "welcome",
      component: Welcome,
      //
      beforeEnter: (to, from, next) => {
        console.log(
          new Date().getHours() +
          ':' + new Date().getMinutes() +
          ':' + new Date().getSeconds() + ":" +
          "beforeEnter");
        next();
      }
    },
    {
      path: "/welcome/:name", //
      name: "welcome",
      component: Welcome
    },
    //APP 
    {
      path: "/music",
      name: "music",
      component: Music
    },
    {
      path: "/music/:type",
      name: "music",
      component: Music,
      children: [{
          path: 'table',
          component: Music
        },
        {
          //  /user/:id/posts 
          // UserPosts  User  <router-view> 
          path: 'detail',
          component: Music
        }
      ]
    },
    {
      path: "/know",
      name: "know",
      component: Know,
    },
    {
      path: "/point",
      name: "point",
      component: Point,
    },
    {
      path: "/user",
      name: "user",
      component: User,
      children: [{
          path: 'info/:id',
          component: UserInfo,
          name: "userinfo"
        },
        {
          //  /user/:id/posts 
          // UserPosts  User  <router-view> 
          path: 'list',
          component: UserList,
          name: "userlist"
        }
      ]
    }
  ]
});


