<template>
    <div class="form-group" :class="{'has-error':validateError}">
        <template v-if="!isHorizontal">
            <label class="control-label text-left" :for="name" >{{label}}</label>
            <p :data-a="isHorizontal">
                <slot></slot>
            </p>
            <transition name="errorinfo">
                <span class="help-block" v-if="!isHorizontal">{{errorMessage}}</span>
            </transition>
        </template>
        <template v-if="isHorizontal">
            <label class="control-label text-right" :for="name" :style="{'width':labelWidth+'%'}" v-if="isHorizontal">{{label}}</label>
            <div class="control-block" :style="{'width':(99-labelWidth)+'%'}" v-if="isHorizontal">
                <p :data-a="isHorizontal">
                    <slot></slot>
                </p>
                <transition name="errorinfo" mode="">
                    <span class="help-block" v-show="validateError">{{errorMessage}}</span>
                </transition>
            </div>
        </template>
    </div>
</template>
<script>
    import Emitter from './../mixins/emitter.js';
    export default {
        name: "HtFormGroup",
        mixins: [Emitter],
        props: {
            //
            label: String,
            labelWidth: String,
            name: {
                type: String,
                required: true
            }, //name
            isCheck: {
                //
                type: Boolean,
                default: false
            },
            // 
            // 
            errorMessages: {
                type: String,
                default: ","
            },
            min: String,
            max: String,
            maxlength: String,
            minlength: String,
            require: Boolean, //TODO 
            pattern: String,
            //
            requireMessage: String,
            patternMessage: String,
            minMessage: String,
            maxMessage: String,
            maxlengthMessage: String,
            minlengthMessage: String
        },
        data: function() {
            return {
                validateError: false,
                errorMessage: this.errorMessages,
                errorMessageList: [],
                isHorizontal: false,
                inputType: ""
            }
        },
        mounted: function() {
            //TODO  
            //NOTE    requireMessage
            
            //horizontal,isHorizontal
            this.isHorizontal = this.$parent.$options.propsData["horizontal"];
            this.$on('validate', function(infoType) {
                // console.log("[" + this.name + "]");
                this.validate(infoType);
            });
            this.$on('clearValidate', function() {
                this.clearValidate();
            });
        },
        methods: {
            dispatch(componentName, eventName, params) {
                var parent = this.$parent || this.$root;
                var name = parent.$options.name;
                while (parent && (!name || name !== componentName)) {
                    parent = parent.$parent;
                    if (parent) {
                        name = parent.$options.name;
                    }
                }
                if (parent) {
                    parent.$emit.apply(parent, [eventName].concat(params));
                }
            },
            //
            clearValidate: function() {
                var _self = this;
                _self.validateError = false;
            },
            /**
             * NAMEinput(,,)
             * NOTE ,sync,
             * @returns
             */
            getDomValue(name) {
                var _self = this;
                var $modelDom = $(_self.$el).find("[name='" + name + "']");
                var type = $modelDom.prop('type');
                _self.inputType = type;
                switch (type) {
                    case "radio":
                        return $(_self.$el).find("[name='" + name + "']:checked").val();
                        break;
                    case "checkbox":
                        var item = [];
                        $('[name="' + name + '"]').each(function() {
                            if ($(this).is(":checked")) {
                                item.push($(this).val());
                            }
                        });
                        return item; // , 
                        break;
                    default:
                        return $.trim($modelDom.val());
                        break;
                }
            },
            /**
             * 
             * @returns
             */
            validate: function(infoType) {
                var _self = this;
                _self.validateError = false;
                _self.errorMessage = "";
                _self.errorMessageList = [];
                var value = _self.getDomValue(_self.name); //
                console.log('[' + _self.name + ']:' + value);
                //
                if (_self.require) {
                    if (!value || value.length == 0) {
                        _self.validateError = true;
                        _self.errorMessageList.push(_self.requireMessage ? _self.requireMessage : "");
                    }
                }
                //
                if (_self.min) {
                    if (!isNaN(_self.min)) {
                        if (_self.inputType != 'checkbox') {
                            //checkbox
                            if (!isNaN(value)) {
                                if (Number(value) < Number(_self.min)) {
                                    _self.validateError = true;
                                    _self.errorMessage = _self.minMessage ? _self.minMessage : "" + _self.min;
                                    _self.errorMessageList.push(_self.errorMessage);
                                }
                            } else {
                                _self.validateError = true;
                                _self.errorMessageList.push("");
                            }
                        } else {
                            if (value == [] || value.length < Number(_self.min)) {
                                _self.validateError = true;
                                _self.errorMessage = _self.minMessage ? _self.minMessage : "" + _self.min + "";
                                _self.errorMessageList.push(_self.errorMessage);
                            }
                        }
                    } else {
                        console.error('ERROR:[' + _self.name + 'min]');
                        return false;
                    }
                }
                //
                if (_self.max) {
                    if (!isNaN(_self.max)) {
                        if (_self.inputType != 'checkbox') {
                            if (!isNaN(value)) {
                                if (Number(value) > Number(_self.max)) {
                                    _self.validateError = true;
                                    _self.errorMessageList.push(_self.maxMessage ? _self.maxMessage : "" + _self.max);
                                }
                            } else {
                                _self.validateError = true;
                                _self.errorMessageList.push("");
                            }
                        } else {
                            if (value == [] || value.length > Number(_self.max)) {
                                _self.validateError = true;
                                _self.errorMessage = _self.maxMessage ? _self.maxMessage : "" + _self.max + "";
                                _self.errorMessageList.push(_self.errorMessage);
                            }
                        }
                    } else {
                        console.error('ERROR:[' + _self.name + 'max]');
                        return false;
                    }
                }
                //
                if (_self.minlength) {
                    if (!isNaN(_self.minlength)) {
                        if (value.length < Number(_self.minlength)) {
                            _self.validateError = true;
                            _self.errorMessageList.push(_self.minlengthMessage ? _self.minlengthMessage : "");
                        }
                    } else {
                        console.error('ERROR:[' + _self.name + 'minlength]');
                        return false;
                    }
                }
                //
                if (_self.maxlength) {
                    if (!isNaN(_self.maxlength)) {
                        if (value.length > Number(_self.maxlength)) {
                            _self.validateError = true;
                            _self.errorMessageList.push(_self.maxlengthMessage ? _self.maxlengthMessage : "");
                        }
                    } else {
                        console.error('ERROR:[' + _self.name + 'maxlength]');
                        return false;
                    }
                }
                //
                if (_self.pattern) {
                    var reg = new RegExp(_self.pattern)
                    if (!(reg.test(value))) {
                        _self.validateError = true;
                        _self.errorMessageList.push(_self.patternMessage ? _self.patternMessage : ",");
                    }
                }
                //
                _self.errorMessage = _self.errorMessageList[0] || "";
                var checkResultObj = {
                    itemName: _self.name,
                    hasError: _self.validateError,
                    errorMessage: _self.errorMessage
                }
                var resultObj = {
                    name: _self.name,
                    value: value
                }
                //,
                if (infoType == "alert") {
                    _self.errorMessage = "";
                    _self.validateError = false;
                }
                //
                _self.dispatch('HtForm', 'getresult', [checkResultObj, resultObj]);
            }
        }
    }
</script>
<style scoped>
    /*  */
    .errorinfo-enter-active,
    .errorinfo-leave-active {
        transform: translate3d(0, 0, 0);
        -webkit-transform: translate3d(0, 0, 0);
        opacity: 1;
        transition: all .5s ease;
    }
    /*  */
    .errorinfo-enter,
    .errorinfo-leave-active {
        transform: translate3d(0, -50%, 0);
        -webkit-transform: translate3d(0, -50%, 0);
        opacity: 0;
    }
    .form-horizontal .control-label {
        display: inline-block;
        width: 25%;
        vertical-align: top;
        padding-top: 0;
    }
    .control-block {
        display: inline-block;
        width: 75%;
        padding: 0 15px;
    }
    .help-block {
        display: none;
        margin: 0;
    }
    .has-error .help-block,
    .has-success .help-block {
        display: block;
    }
    .has-error .el-checkbox {
        color: #a94442;
    }
    p {
        margin-bottom: 0;
    }
</style>


