//HtmlFun
/**
 * Html
 * @returns
 */
window.HtmlFun = {
  //
  extend: function(obj) {
    if (obj) {
      HtmlFun = $.extend(HtmlFun, obj);
    }
  },
  toGender: function(value) {
    return value == "M" ? "" : "";
  },
  /**
   *  
   * @param {any} shijianchuo   
   * @returns 
   */
  toNormalTime: function(shijianchuo) {
    var time = new Date(parseInt(shijianchuo));
    var y = time.getFullYear();
    var m = time.getMonth() + 1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s);
  },
  /**
   * 
   * @returns
   */
  toImg: function(value) {
    var str = "<img src=" + value + "/>";
    return str;
  },
  testMultiParams: function(id, name, problem) {
    return '<a href="javascript:;" onclick="HtmlFun.test(\'' + id + '\',\'' + name + '\',\'' + problem + '\')"></a>';
  },
  add0: function(m) {
    return m < 10 ? '0' + m : m;
  },
  test(id, name, problem) {
    alert('id' + id + '\r\n' + name + '\r\n' + problem);
  }
}


