/**
 * Vuex
 * @returns
 */

import Vue from 'vue';
import $ from 'jquery';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    count: 0,
    name: "",
    nameList: [
      '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
    ]
  },
  //NOTE  mutation 
  strict: true,
  /**
   * storecommit,  
   * @returns
   */
  mutations: {
    addNum(state) {
      state.count++
    },
    //NOTE 
    //,name  
    // 
    changeName(state) {
      state.name = state.nameList[parseInt(Math.random() * 25)];
    }
  }
});


