import Vue from 'vue';
import $ from 'jquery';
Vue.directive('btnDelay', {
  bind: (el, binding, vnode, oldVnode) => {
    let beforeText = el.innerHTML, //BUTTONtext
      secondNum = binding.value; //
    binding.value || (console.error("v-btn-delay ") || el.setAttribute("disabled", true));
    $(el).click((e) => {
      e.preventDefault();
      settime(el);
    });
    //
    function settime(obj) {
      if (binding.value == 0) {
        obj.removeAttribute("disabled");
        obj.innerHTML = beforeText;
        binding.value = secondNum;
        return;
      } else {
        obj.setAttribute("disabled", true);
        obj.innerHTML = "(" + binding.value + "s)";
        binding.value--;
      }
      setTimeout(() => settime(obj), 1000);
    }
  }
});


