<template>
    <div class="text-center well container">
        <b class="pull-right">:user/info/:id</b>
        <h3>{{userInfoObj.name}},,: {{userInfoObj.age}},:{{$route.params.id}}</h3>
        <img :src="userInfoObj.src" alt="" class="img-thumbnail">
        <p>
            <router-link to="/user/list" class="btn btn-link"></router-link>
            <router-link to="/user/info/155" class="btn btn-link">ID:155,beforeRouteUpdate</router-link>
        </p>
    </div>
</template>
<script>
    import router from './../router/index.js';
    export default {
        name: "UserInfo",
        router,
        props: {
            //
        },
        data: function() {
            //
            return {
                userInfoObj: {
                    name: "",
                    age: 0
                }
            }
        },
        mounted: function() {
            //
            this.getInfo();
        },
        methods: {
            getInfo() {
                //
                let userListObj = {
                    1: {
                        "name": "",
                        "age": "35",
                        "src": "http://om6fr85l4.bkt.clouddn.com/singer/zhoujielun.jpg"
                    },
                    2: {
                        "name": "",
                        "age": "30",
                        "src": "http://om6fr85l4.bkt.clouddn.com/singer/caiyilin.jpg"
                    },
                    3: {
                        "name": "",
                        "age": "55",
                        "src": "http://om6fr85l4.bkt.clouddn.com/singer/liudehua.jpg"
                    },
                    4: {
                        "name": "",
                        "age": "18",
                        "src": "http://om6fr85l4.bkt.clouddn.com/singer/niniimg.jpg"
                    },
                    5: {
                        "name": "",
                        "age": "43",
                        "src": "http://om6fr85l4.bkt.clouddn.com/singer/suyang.jpg"
                    },
                    155: {
                        "name": "",
                        "age": "200",
                        "src": "http://om6fr85l4.bkt.clouddn.com/singer/zongguanxian.jpg"
                    }
                }
                const userid = this.$route.params.id;
                userid && (this.userInfoObj = userListObj[userid]);
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                // this, `vm` 
                // console.log(vm);    // 
                // console.log(this);  //this,undefined
                //NOTE  ,
                if (!vm.$route.params.id) {
                    alert('ID,...');
                   router.push({
                        path: '/user/list'
                    });
                }


                console.log(router);
                console.log(vm.$route);
            });
        },
        beforeRouteUpdate(to, from, next) {
            // 
            //  /foo/:id /foo/1  /foo/2 
            //  Foo 
            //  `this`
            // next();
            console.log("");
            console.log(to);
            console.log("");
            //ID
            console.log("");
            console.log(to.params.id);
            console.log("");
            if(to.params.id){
                next();
                this.getInfo();
            }else{
                alert('ID,...');
                router.push({
                    path: '/user/list'
                });
            }
            
        },
        beforeRouteLeave(to, from, next) {
            var _self = this;
            //TODO 2017121017:28:50 https://router.vuejs.org/zh-cn/advanced/navigation-guards.html 
            // this,,
            // beforeRouteEnterthis,this
            // ,
            // NOTE   beforeRouteLeave:(to, from, next)=> {...}, ,this,this,beforeRouteLeave(to, from, next){...}
            //NOTE this :http://blog.csdn.net/liwusen/article/details/70257837
            // console.log(self.a.data().userInfoObj);
            // console.log(_self);
            let result = _self.userInfoObj.name ? confirm(_self.userInfoObj.name + ",?") : true;
            next(result);
        }
    }
</script>
<style scoped>

</style>


