<template>
	<div id="app" class="container" v-cloak>
		<div class="row">
			<!--  -->
			<div class="col-md-12">
				<!-- Nav tabs -->
				<ul class="nav nav-tabs" role="tablist">
					<li role="presentation" class="active"><a href="#home" aria-controls="home" role="tab" data-toggle="tab"></a></li>
					<li role="presentation"><a href="#profile" aria-controls="profile" role="tab" data-toggle="tab"></a></li>
					<li role="presentation" class=""><a href="#messages" aria-controls="messages" role="tab" data-toggle="tab"></a></li>
				</ul>
				<!-- Tab panes -->
				<div class="tab-content">
					<!--  -->
					<div role="tabpanel" class="tab-pane active" id="home">
						<div class="row">
							<div class="col-md-6"> 
								<!--  -->
								<panel title="">
									<!--  -->
									<h3 style="margin-bottom:15px;text-align:center;">CF</h3>
									<!-- Form -->
									<ht-form ref="wuqiku" info-type="info" :horizontal="true">
										<ht-form-group label="M4A1-" label-width="20" name="lanzuan" :require="true" require-message="">
											<input type="text" name="lanzuan" v-model="lanzuan" class="form-control" placeholder="">
										</ht-form-group>
										<ht-form-group label="9A91-S" label-width="20" name="aaa" min="91" max="200" max-message="">
											<input type="text" name="aaa" v-model="aaa" class="form-control" placeholder="">
										</ht-form-group>
										<ht-form-group label="" label-width="20" name="jinggun" maxlength="10" maxlength-message="10" minlength="3" :require="true">
											<input type="text" name="jinggun" v-model="jinggun" class="form-control" placeholder="">
										</ht-form-group>
										<ht-form-group label="M240B-" label-width="20" name="canglei" minlength="10">
											<textarea rows="5" cols="15" v-model="canglei" class="form-control" name="canglei"></textarea>
										</ht-form-group>
										<ht-form-group label="" label-width="20" name="kuanglong" pattern="^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])" pattern-message="">
											<input type="text" name="kuanglong" v-model="kuanglong" class="form-control" placeholder="">
										</ht-form-group>
										<ht-form-group label="M21" label-width="20" name="weiya" :require="true" require-message="">
											<select class="form-control" name="weiya">
														<option value=""></option>
														<option value="FR-F2 ">FR-F2 </option>
														<option value=""></option>
														<option value=""></option>
														<option value=""></option>
													</select>
										</ht-form-group>
										<ht-form-group label="" label-width="20" name="cfWuqi" :require="true" require-message="" min='2' min-message="CF">
											<label v-for="x in formPersonsList">
														<ht-checkbox v-model="formTotalPrice" :label='x.price' name='cfWuqi'> {{x.name+'('+x.price+')'}}</ht-checkbox>
													</label>
										</ht-form-group>
										<ht-form-group label="" label-width="20" name="payWay" :require="true" require-message="">
											<ht-radio v-model="payWay" label="1" name="payWay"></ht-radio>
											<ht-radio v-model="payWay" label="2" name="payWay"></ht-radio>
										</ht-form-group>
										<hr>
										<div class="text-right">
											<button type="button" class="btn btn-default" @click="cancelCheckForm('wuqiku')"></button>
											<button type="button" class="btn btn-large btn-primary" @click="submitForm('wuqiku')"></button>
										</div>
									</ht-form>
								</panel>
								</panel>
								<panel title="">
									<ht-radio v-model="radio" label="1" @click="alerts()" @change="change()" name="cfPerson"></ht-radio>
									<ht-radio v-model="radio" label="2" @click="alerts()" @change="change()" name="cfPerson"></ht-radio>
									<ht-radio v-model="radio" label="3" @click="alerts()" @change="change()" name="cfPerson"></ht-radio>
									<ht-radio v-model="radio" label="4" @click="alerts()" @change="change()" name="cfPerson"></ht-radio>
									<p>:{{radio}}</p>
									<div id="clickRecord" class="col-md-6"></div>
									<div id="changeRecord" class="col-md-6"></div>
								</panel>
								<panel title="">
									<label v-for="x in PersonsList">
									<ht-checkbox v-model="totalPrice" :label='x.price' name='cfWuqis'> {{x.name+'('+x.price+')'}}</ht-checkbox>
									</label>
									<p>  {{totalPrice.sum()}}</p>
								</panel>
							</div>
							<div class="col-md-6">
								<panel title="">
									<button type="button" slot="header" class="btn-link pull-right" @click="changePage" style="outline: none;"></button>
									<ht-page :param="pageOptions"></ht-page>
									<!-- <button type="button" class="btn btn-primary pull-right" @click="changePage"></button> -->
									<!-- <div slot="footer"></div> -->
								</panel>
								<panel title="Modal">
									<div class="text-center">
										<button type="button" class="btn btn-success " @click="modalControl()">modal</button></div>
									<!-- Modal -->
								</panel>
								<ht-modal :is-show="showModal" @states="modalControl" title="">
									<!-- <p>&lt;ht-modal&gt;&lt;app&gt;</p> -->
									<img :src="require('./../assets/img/usericon/usericon_2.jpg')" alt="" class="img-responsive">
								</ht-modal>
								<!--  -->
								<panel title="" style="background-color: #ddd;">
									<info-block :img-src="require('./../assets/img/usericon/xun.jpg')" title="" intro="Eason,," word=""
									    style="width:32%;margin-left: 1%;"></info-block>
									<info-block :img-src="require('./../assets/img/usericon/zhou.jpg')" title="" intro="," word="       " style="width:32%;"></info-block>
									<info-block :img-src="require('./../assets/img/usericon/qian.jpg')" title="" intro="" word=" "
									    style="width:32%;"></info-block>
								</panel>

								<panel title="">
									<button type="button" class="btn btn-primary" @click="notify()"></button>
									<button type="button" class="btn btn-primary" @click="notifyRight()"></button>
								</panel>
							</div>
						</div>
					</div>
					<!--  -->
					<div role="tabpanel" class="tab-pane" id="profile">
						<div class="row">
							<div class="col-md-2">
								<panel title="">
									<pr-list ref="pianduan">
										<pr slot min="1" length="2" :has-error="false" message=""></pr>
										<pr slot min="32" length="10" message="something wrong!"></pr>
										<pr slot="red" min="3333" length="3" message="something3333!">slot='red'</pr>
									</pr-list>
									<button type="button" class="btn btn-success" @click="likai('pianduan')">BUTTON</button>
								</panel>
							</div>
							<div class="col-md-6">
								<div class="panel panel-default" style="margin-top: 15px;">
									<div class="panel-heading"></div>
									<div class="panel-body">
										<ajax-search @suggestClick="suggestClick"></ajax-search>
										<panel title="">
											<div class="row" style="margin-top: 15px;" v-show="song.showDetail">
												<div class="col-md-3">
													<p class="text-right"><img :src="song.songImg" alt="" class="img-responsive img-thumbnail" style="width:100px;height: 100px;"></p>
												</div>
												<div class="col-md-9">
													<p>:{{song.name}}</p>
													<p>:{{song.author}}</p>
													<p style="word-break: break-all;">: <a :href="song.url" class="btn-link" v-show="song.name" :download="song.name+'.mp3'"></a> </p>
												</div>
											</div>
											<!--  -->
											<div style="position: relative; height: 100px;" v-show="!song.showDetail">
												<loading></loading>
											</div>
										</panel>
									</div>
								</div>
							</div>
							<div class="col-md-4">
								<button type="button" class="btn btn-primary btn-block" @click="changeName">sote</button>
								<panel title=".sync">
									<textarea type="text" v-model="inputMsg" class="form-control"></textarea>
									<p></p>
									<al :message.sync="inputMsg"></al>
								</panel>
							</div>
						</div>
					</div>
					<!--  -->
					<div role="tabpanel" class="tab-pane" id="messages">
						<panel title="">
							<span slot="header" class="pull-right"><b>201711811:25:07 </b></span>
							<panel title="">
								<ht-table slot="outer" ajaxurl="ma/users" :search-data="searchData" class="">
									<!---->
									<!-- http://localhost:3001/users/allUser -->
									<column slot name="" data-key="name" width="15%" align="left" class="nameClass aaa" style="color:red;text-align:right; "></column>
									<column slot name="" data-key="gender" align="center" filter="toGender"></column>
									<column slot name="" data-key="Regtime" align="center" filter="toNormalTime"></column>
									<column slot name="" data-key="iconUrl" align="center" filter="toImg"></column>
									<column slot name="" data-key="ID,name,Regtime" align="center" filter="testMultiParams"></column>
								</ht-table>
							</panel>
						</panel>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import router from './../router/index.js';
	import HtDate from './../ht/HtDate.vue';
	import store from './../store/index';
	export default {
		name: "Main",
		props: {
			//
		},
		data: function() {
			//
			return {
				inputMsg: "",
				song: {
					id: "",
					name: "",
					author: "",
					songImg: "",
					url: "",
					showDetail: true
				},
				showModal: false,
				age: 45,
				zhoujielun: "",
				price: 188,
				lanzuan: 15,
				aaa: 156,
				jinggun: 213,
				canglei: 8888888888888888,
				kuanglong: '321@qq.com',
				linghu: "1",
				payWay: "1",
				searchData: {
					currentPage: 1
				},
				searchWord: "",
				show: true,
				// :
				pageOptions: {
					currentPage: 1,
					totalPage: 10,
					showItem: 5,
					// prevText: "",
					// nextText: "",
					click: function(currentPage) {
						console.log(":" + currentPage);
					}
				},
				timepicker: "2017/11/14",
				radio: "2",
				totalPrice: [58],
				PersonsList: [{
					name: "",
					price: 38
				}],
				formPersonsList: [{
					"name": "",
					"price": 38
				}, {
					"name": "",
					"price": 58
				}, {
					"name": "",
					"price": 108
				}, {
					"name": "",
					"price": 18
				}, {
					"name": "",
					"price": 36
				}, {
					"name": "",
					"price": 19
				}],
				formTotalPrice: [19]
			}
		},
		mounted: function() {
			this.getPerson();
		},
		methods: {
			/**
			 * Vuex 
			 * commit,
			 * @returns
			 */
			changeName() {
				store.commit('changeName');
			},
			getPerson() {
				var _self = this;
				$.ajax({
					type: "GET",
					url: "/ma/person",
					dataType: "json",
					success: function(data) {
						if (data != null && data != "") {
							_self.PersonsList = data;
						} else {
							// _self.PersonsList = [];
							_self.PersonsList=[{"name":"","price":38},{"name":"","price":58},{"name":"","price":108},{"name":"","price":18},{"name":"","price":36},{"name":"","price":19}]
						}
					},
					error: function(response) {
						console.log("Mian.vue,");
						_self.PersonsList=[{"name":"","price":38},{"name":"","price":58},{"name":"","price":108},{"name":"","price":18},{"name":"","price":36},{"name":"","price":19}]
					}
				});
			},
			//JQDOM  
			alerts: function alerts(e) {
				$("#clickRecord").append('<p><b>@click</b><b></b>' + this.radio + '</p>');
			},
			change: function change() {
				$("#changeRecord").append('<p><b>@change</b>' + this.radio + '</p>');
				return false;
			},
			daterSelect: function(val) {
				alert('' + val);
			},
			suggestClick: function(state, result) {
				if (state == 'begin') {
					this.song.showDetail = false;
					this.song.name = '...';
					this.song.author = '';
					this.song.songImg = '';
					this.song.url = '';
					this.song.id = '';
				} else if (state == 'end') {
					//
					this.song.id = result.id;
					this.song.name = result.name;
					this.song.author = result.author;
					this.song.songImg = result.songImg;
					this.song.url = result.url;
					this.song.showDetail = true;
				}
			},
			changePage: function() {
				var num = parseInt(Math.random() * 20 + 1);
				this.pageOptions.currentPage = num;
				this.pageOptions.totalPage = parseInt(Math.random() * (40 - 30) + 30);
				/**
				 * 
				 * 
				 * @param {any} start 
				 * @param {any} end 
				 */
				function radomNum(start, end) {
					end = start > end ? [start, start = end][0] : end; //End  
					return
				}
			},
			modalControl: function() {
				this.showModal = !this.showModal;
			},
			likai: function(formName) {
				this.$refs[formName].check();
			},
			submitForm: function(formName) {
				this.$refs[formName].checkItem(function(isPass, errorList) {
					if (isPass) {
						console.log("");
					} else {
					var n=	this.$notifyMessage("<b></b><br>" + errorList.join('<br>'));
					 console.log("");
					 console.log(n);
					 console.log("");
					 
					}
				});
			},
			cancelCheckForm: function(formName) {
				this.$refs[formName].clearValidate();
			},
			test(id, name) {
				alert('')
			},
			search: function(value) {
				console.log(
					new Date().getHours() +
					':' + new Date().getMinutes() +
					':' + new Date().getSeconds() + ":" +
					value);
			},
			notify() { 
                this.$notifyMessageLeft("")
            },
            notifyRight(){
                // this.$notify({
                //     direction:"right",
                //     message:""
                // })
                this.$notifyMessage("");
            }
		},
		components: {
			HtDate
		}
	}
</script>
<style scoped>
	[v-cloak] {
		display: none;
	}
	.fade-enter-active,
	.fade-leave-active {
		transition: all 0.5s ease;
	}
	.fade-enter,
	.fade-leave-active {
		opacity: 0
	}
</style>


